          <h3 @click="test" style="color: white; word-break: keep-all; text-overflow: ellipsis;">后台管理系统</h3>
        </div>
        <div class="flex-rCenter">
          <el-tooltip class="item" effect="dark" content="全屏" placement="bottom">
            <el-button style="background-color: transparent; transform: scale(1.2); border: none" @click="toggleFullScreen" circle icon="el-icon-full-screen"></el-button>
          </el-tooltip>
          <el-dropdown style="margin-right: 1rem">
            <div class="flex-rCenter">
              <img style="margin-right: 0.5rem;border-radius: 50%; width: 30px; height: 30px;" src="@/assets/images/avatar.jpg" alt="">
