              <el-dropdown-menu>
                <el-dropdown-item icon="el-icon-s-custom">个人资料</el-dropdown-item>
                <el-dropdown-item icon="el-icon-sold-out" @click="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </nav>
      <div class="breadcrumb flex-rCenter">
        <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 1rem">
          <template v-for="route in router.getRoutes()" :key="route.name">
            <templaet v-if="route.name==router.currentRoute.value.name">
              <templaet v-if="route.meta.breadcrumb">
                <el-breadcrumb-item :to="item.path?{path:item.path}:null" v-for="item in route.meta.breadcrumb" :key="item.title">{{ item.title }}</el-breadcrumb-item>
              </templaet>
              <templaet v-else>
