<script lang="ts">
import {
  reactive,
  defineComponent,
  watch,
  ref,
  markRaw,
  onBeforeUpdate,
  inject,
  onBeforeMount,
  computed,
  onUpdated
} from 'vue';
import Statistics from '@/components/Statistics.vue';
import {Router, useRouter } from "vue-router";
import {Store, useStore} from "vuex";
import NProgress from "nprogress";
import nProgress from "nprogress";

export default defineComponent({
  name: 'Layout',
  setup(props, context) {
    const router:Router = useRouter();
    const store:Store<any> = useStore();
    const velocity:any = inject('velocity');
    const currenMediaWidth = computed(()=> store.state.currenMediaWidth);
    let data = reactive({
      isCollapse: false,
      currentRoute: router.currentRoute.value
    });
    onBeforeMount(()=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    onBeforeUpdate(()=>{
      data.currentRoute = router.currentRoute.value;
    });
    onUpdated(()=>{
      NProgress.start();
      NProgress.done();
    });
    function toggleFullScreen(){
      if (document.fullscreenElement) {
        document.exitFullscreen();
      } else {
        document.documentElement.requestFullscreen();
      }
    }
    function menuSwitch(){
      if (window.matchMedia(store.state.media_md).matches) {
        let menu:any = document.querySelector('.aside-nav');
        let mask:any = document.querySelector('.mask');
        menu.classList.add('aside-nav-open');
        velocity(mask, {
          opacity: 1
        }, {
          duration: 400,
          display: 'block'
        });
      } else {
        data.isCollapse=!data.isCollapse;
      }
    }
    function maskCloseMenu(e:any){
      let menu:any = document.querySelector('.aside-nav');
      let mask:any = e.target;
      menu.classList.remove('aside-nav-open');
      velocity(menu, {
        xxx: null
      }, {
        duration: 400,
        begin: (els:any)=>{
          els[0].style.setProperty('position', 'absolute');
          els[0].style.setProperty('height', '100%');
          els[0].style.setProperty('z-index', '10');
        },
        complete: (els:any)=>{
          els[0].style.setProperty('position', null);
          els[0].style.setProperty('height', null);
          els[0].style.setProperty('z-index', null);
        }
      });
      velocity(mask, {
        opacity: 0
      }, {
        duration: 400,
        display: 'none'
      });
    }
    watch(currenMediaWidth, (nv, ov)=>{
      if (window.matchMedia(store.state.media_md).matches) {
        data.isCollapse = true;
      }else{
        data.isCollapse = false;
      }
    });
    function logout(){
      window.localStorage.removeItem('userToken');
      router.push({path: '/login'});
    }
    function test(){

      console.log('test')

    }
    const handleSelect = (key:any, keyPath:any) => {return null;};
    const handleOpen = (key:any, keyPath:any) => {return null;};
    const handleClose = (key:any, keyPath:any) => {return null;};
    return {
      router,
      data,
      toggleFullScreen,
      menuSwitch,
      maskCloseMenu,
      logout,
      test,
      handleSelect,
      handleOpen,
      handleClose
    };
  },
});


</script>

